import React from 'react';
import { Link, scroller } from "react-scroll";
import logo from './logo.svg';
import cover from './ep.JPG';
import './App.css';
import Header from './components/Header';
import TabbedContent from './components/TabbedContent';
import PortfolioInfo from './components/PortfolioInfo';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper:{
    padding: theme.spacing(4),
    backgroundColor: '#87bdc7', //blue
    //square: true
  },
  white: {
    backgroundColor: 'white',
    paddingLeft: 0,
    paddingRight: 0,
  },
  scrollLink: {
    color: '#fff',
    cursor: 'pointer'
  }
}));

function App() {
  const classes = useStyles();
  
  function handleScroll() {
    //animateScroll.scrollTo(500);
    scroller.scrollTo("scrollDiv", {
      duration: 500,
      smooth: "easeInExpo",
      offset: -80
    });
  }

  return (
    <div className="App">
      <Header />
      <div className="App-content">
        <Grid container alignItems="center">
          <Grid container item xs={12} sm={6}>
            <Container className={classes.white}>
              {<img src={cover} className="App-cover" alt="ep-cover" />}
            </Container>
          </Grid>
          <Grid container item xs={12} sm={6}
            justify="space-evenly"
            className="App-contours">
              <Paper className={classes.paper}>
                <Typography variant="h3" gutterBottom>
                  MapMazeLu
                </Typography>
                <Typography variant="subtitle1" gutterBottom>
                  <code>GIS Professional • Full-Stack Developer • Map Enthusiast</code>
                </Typography>
                {<img src={logo} className="App-logo" alt="logo" />}
                <Typography variant="body1" gutterBottom>
                  B I E N V E N I D O S<br />
                  &amp; thanks for visiting! <br />
                  <PortfolioInfo />
                  <Link
                    className={classes.scrollLink}
                    onClick={handleScroll}>
                      Please, have a look around...
                  </Link>
                </Typography>
              </Paper>
            </Grid>
        </Grid>
      </div>
      <hr></hr>
      <Grid container item xs={12} alignContent="center" id="scrollDiv">
        <TabbedContent />
      </Grid>
    </div>
  );
}

export default App;
