import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    //color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export default function CustomizedDialogs() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button variant="outlined" onClick={handleClickOpen}>
        <InfoIcon />
      </Button>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          What's in here?
        </DialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>
            The <strong>map-centric applications</strong> in this portfolio showcase some of my best public-facing work—projects in which I've taken the Lead Developer role (writing &gt;&#61; 90% code).
          </Typography>
          <Typography gutterBottom>
            While <strong>each project applies a variety of content, layouts and features</strong>, most applications are:
            <ul>
                <li>mobile-ready &amp; device agnostic</li>
                <li>built using tools like Esri's REST/JavaScript APIs</li>
                <li>written with responsive libraries (i.e. Materialize &amp; Bootstrap)</li>
            </ul>
          </Typography>
          <Typography gutterBottom>
            Of course, <em>good looks aren't everything</em>. The UIs you see are complemented by efficient server-side <strong>PHP &amp; Python</strong>/client-side <strong>JavaScript</strong> to process data in <strong>MS SQL &amp; MySQL</strong> databases, as well as <strong>REST &amp; SOAP web services</strong>.
          </Typography>
          <hr/>
          <Typography gutterBottom>
            Lastly, in efforts to pursue new skills and learn new tooling, this portfolio is written using the <strong>React</strong> framework. Enjoy!
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}