import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import { WebMapView } from './MapHistory';
import PortfolioData from './PortfolioData';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `tab-${index}`,
    'aria-controls': `tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  /* root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
    height: 550,
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  }, */
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  tabs:{
    backgroundColor: "#ebebeb",
  }
}));

export default function PortfolioTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <Tabs
        variant="fullWidth"
        value={value}
        indicatorColor="primary"
        onChange={handleChange}
        aria-label="Portfolio tabs"
        centered
      >
        <Tab label="Portfolio" {...a11yProps(0)} />
        <Tab label="Experience" {...a11yProps(1)} />
        {/* <Tab label="Education" {...a11yProps(2)} />
        <Tab label="Extra Bytes" {...a11yProps(3)} /> */}
        <Tab label="Map History" {...a11yProps(2)} />
      </Tabs>
      <TabPanel className={classes.tabs} value={value} index={0}>
        <PortfolioData type={'projects'}/>
      </TabPanel>
      <TabPanel className={classes.tabs} value={value} index={1}>
        <PortfolioData type={'work'}/>
        <br />
        <PortfolioData type={'skills'}/>
        <br />
        <PortfolioData type={'school'}/>
      </TabPanel>
      {/* <TabPanel className={classes.tabs} value={value} index={2}>
        <PortfolioData type={'school'}/>
      </TabPanel>
      <TabPanel className={classes.tabs} value={value} index={3}>
        <PortfolioData type={'skills'}/>
      </TabPanel> */}
      <TabPanel className={classes.tabs} value={value} index={2}>
        <WebMapView />
      </TabPanel>
    </div>
  );
}