import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineOppositeContent from '@material-ui/lab/TimelineOppositeContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import WorkIcon from '@material-ui/icons/Work';
import WorkOutlineIcon from '@material-ui/icons/WorkOutline';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: '6px 16px',
    textAlign: 'center'
  },
  secondaryTail: {
    backgroundColor: theme.palette.secondary.main,
  },
}));

export default function CustomizedTimeline({data}) {
    const classes = useStyles();
    return (
        <Timeline align="alternate">
            {
                data.map(item => (
                    <TimelineItem>
                        <TimelineOppositeContent>
                        <Typography variant="body2" color={(item.end)? "textSecondary" : "textPrimary"}>
                            {(item.end)?item.start + "- " + item.end : "Since " + item.start}
                        </Typography>
                        </TimelineOppositeContent>
                        <TimelineSeparator>
                        <TimelineDot>
                            {(item.end)?<WorkOutlineIcon />: <WorkIcon color="primary"/>}
                        </TimelineDot>
                        <TimelineConnector />
                        </TimelineSeparator>
                        <TimelineContent>
                        <Paper elevation={3} className={classes.paper}>
                            <Typography variant="h6" component="h1">
                                {item.role}
                            </Typography>
                            <Typography>
                                <Link
                                    component="button"
                                    variant="body2"
                                    onClick={() => {
                                        window.open(item.link, "_blank");
                                    }}
                                    >
                                    {item.org}
                                </Link>
                            </Typography>
                        </Paper>
                        </TimelineContent>
                    </TimelineItem>
                ))
            }
        </Timeline>
    );
}
