import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import { Alert, AlertTitle } from '@material-ui/lab';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Typography from '@material-ui/core/Typography';
import DesktopMacIcon from '@material-ui/icons/DesktopMac';
import EmojiEventsIcon from '@material-ui/icons/EmojiEvents';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import FlareIcon from '@material-ui/icons/Flare';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import VerifiedUserIcon from '@material-ui/icons/VerifiedUser';

const useStyles = makeStyles((theme) => ({
  root: {
    //width: '100%',
    flexGrow: 1,
    alignItems: 'flext-start'
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    //flexBasis: '50%',
    flexShrink: 0,
  },
  secondaryHeading: {
    display: 'block',
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
    //fontStyle: 'italic'
  },
  detailsItem: {
      margin: '0 5px'
  },
  details: {
      display: 'inline-flex',
      padding: '5px 5px',
  }
}));

export default function AccordionSkills({data}) {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    const awards = data.filter(item => {
        return item.type === "award";
    });
    const certs = data.filter(item => {
        return item.type === "certification";
    });
    const skills = data.filter(item => {
        return item.type === "skill";
    });
    const langs = data.filter(item => {
        return item.type === "language";
    });
    const softies = data.filter(item => {
        return item.type === "soft";
    });

    return (
        <div container className={classes.root}>
            <Accordion expanded={expanded === 'certsPanel'} onChange={handleChange('certsPanel')}>
                <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="certsPanel-content"
                id="certsPanel-header"
                >
                <Typography className={classes.heading}>Awards &amp; Certifications</Typography>
                </AccordionSummary>
                <AccordionDetails  class={classes.details}>
                    <List>
                        <ListItem>
                            {
                                certs.map( item => (
                                    <Alert icon={false} severity="info" gutterBottom>
                                        <VerifiedUserIcon />
                                        <AlertTitle>{item.start}</AlertTitle>
                                        <strong>{item.org}</strong> - {item.name}
                                    </Alert>
                                ))
                            }
                        </ListItem>
                        <ListItem>
                            {
                                awards.map( item => (
                                    <Alert icon={false} severity="info" gutterBottom>
                                        <EmojiEventsIcon />
                                        <AlertTitle>{item.start}</AlertTitle>
                                        <a className={classes.secondaryHeading} href={item.link} target="_blank" rel="noopener noreferrer"><strong>{item.org}</strong></a> {item.name}
                                    </Alert>
                                ))
                            }
                        </ListItem>
                    </List> 
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'langsPanel'} onChange={handleChange('langsPanel')}>
                <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="langsPanel-content"
                id="langsPanel-header"
                >
                    <Typography className={classes.heading}>Languages, Software Skills &amp; Other Talents</Typography>
                </AccordionSummary>
                <AccordionDetails class={classes.details}>
                    <List>
                        <ListItem>
                            {
                                langs.map( item => (
                                    <Alert icon={false} severity="info" gutterBottom>
                                        <LibraryBooksIcon />
                                        <AlertTitle>{item.name}</AlertTitle>
                                        {item.desc}
                                    </Alert>
                                ))
                            }
                        </ListItem>
                        <ListItem>
                            {
                                skills.map( item => (
                                    <Alert icon={false} severity="info" gutterBottom>
                                        <DesktopMacIcon />
                                        <AlertTitle>{item.name}</AlertTitle>
                                        {item.desc}
                                    </Alert>
                                ))
                            }
                        </ListItem>
                        <ListItem>
                            {
                                softies.map( item => (
                                    <Alert icon={false} severity="info" gutterBottom>
                                        <FlareIcon />
                                        <AlertTitle>{item.name}</AlertTitle>
                                    </Alert>
                                ))
                            }
                        </ListItem>
                    </List> 
                </AccordionDetails>
            </Accordion>
        </div>
    );
}
