import React from 'react';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import CircularProgress from '@material-ui/core/CircularProgress';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FlightTakeoffIcon from '@material-ui/icons/FlightTakeoff';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import Typography from '@material-ui/core/Typography';
import { loadModules } from 'esri-loader';

export class WebMapView extends React.Component {
  constructor(props) {
    super(props);
    this.mapRef = React.createRef();
    this.state = {
        isLoaded: false,
    };
  }
  renderLoader(){
    return (
        <CircularProgress color="primary"/>
    );
  }
  componentDidMount() {
    // lazy load the required ArcGIS API for JavaScript modules and CSS
    loadModules(['esri/Map', 'esri/views/MapView', 'esri/layers/CSVLayer', 'esri/widgets/Home'], { css: true })
    .then(([ArcGISMap, MapView, CSVLayer, Home]) => {
      this.setState({ isLoaded: true });
      
      const map = new ArcGISMap({
        basemap: 'gray-vector'
      });

      var popupTemplate = {
        // autocasts as new PopupTemplate()
        title: "Where I.. {Status}",
        content: [
          {
            type: "fields",
            fieldInfos: [
              {
                fieldName: "Description",
                label: "Significance"
              },
              {
                fieldName: "Link",
                label: "Check it out",
                /* format: {
                  digitSeparator: true,
                  places: 0
                } */
              }
            ]
          }
        ]
      };

      this.view = new MapView({
        container: this.mapRef.current,
        map: map,
        center: [-79.79, 32.75],
        zoom: 3
      });

      var url = "https://raw.githubusercontent.com/emmalu/data/master/EP.csv";

      var csvLayer = new CSVLayer({
        url: url,
        copyright: "MapMazeLu",
        popupTemplate: popupTemplate
      });
      csvLayer.renderer = {
        type: "simple",  // autocasts as new SimpleRenderer()
        symbol: {
          type: "simple-marker",  // autocasts as new SimpleMarkerSymbol()
          size: 10,
          color: "#87bdc7",
          outline: {  // autocasts as new SimpleLineSymbol()
            width: 0.5,
            color: "#255D69"
          }
        }
      };
      map.add(csvLayer);

      var homeWidget = new Home({
        view: this.view
      });
      this.view.ui.add(homeWidget, 'top-left');
      this.view.ui.add(test, 'bottom-right');
    });
  }
  

  componentWillUnmount() {
    if (this.view) {
      // destroy the map view
      this.view.container = null;
    }
  }

  render() {
    const isLoaded = this.state.isLoaded;
    let content = this.renderLoader();
    if(isLoaded){
      //display map here
      content = <div className="webmap" ref={this.mapRef}>
        <Box id="test" className="mapInfo">
          <Typography variant="h6" alignItems="right">
            This map highlights some of my journey through space.
          </Typography>
          <Typography variant="body1" alignItems="right">
            The marked locations are special places—important in the making of... me.
          </Typography>
          <br />
          <Grid container
            direction="row-reverse"
            justify="space-between"
            alignItems="center">
            <Grid item>
              <div class="esri-widget--button esri-widget esri-interactive esri-component">
                <FavoriteIcon />
              </div>
            </Grid>
            <Grid item>
              Places close to my heart
            </Grid>
          </Grid>
          <Grid container
            direction="row-reverse"
            justify="space-between"
            alignItems="center">
            <Grid item>
              <div class="esri-widget--button esri-widget esri-interactive esri-component">
                <MenuBookIcon />
              </div>
            </Grid>
            <Grid item>
              Where I've been schooled
            </Grid>
          </Grid>
          <Grid container
            direction="row-reverse"
            justify="space-between"
            alignItems="center">
            <Grid item>
              <div class="esri-widget--button esri-widget esri-interactive esri-component">
                <BusinessCenterIcon />
              </div>
            </Grid>
            <Grid item>
              Where I've worked
            </Grid>
          </Grid>
          <Grid container
            direction="row-reverse"
            justify="space-between"
            alignItems="center">
            <Grid item>
              <div class="esri-widget--button esri-widget esri-interactive esri-component">
                <FlightTakeoffIcon />
              </div>
            </Grid>
            <Grid item>
              Favorite destinations
            </Grid>
          </Grid>
        </Box>
      </div>;
    }
    return (
      content
    );
  }
}